<template>
  <div name="affiliations">
    <CoverImage page="Affiliations" backgroundImage="backgnd_3.jpg" title="Our Affiliations" />

    <v-container>
      <br />
      <br />
      <v-layout row wrap class="ma-2">
        <!-- Affiliations Page -->
        <v-flex>
          <v-img
            :src="require('@/assets/images/affiliations/affiliations.png')"
            alt="Our Affiliations"
          />
        </v-flex>
      </v-layout>
    </v-container>

    <br />
    <br />
  </div>
</template>

<script>
import CoverImage from "@/components/common/CoverImage.vue";

export default {
  name: "Affiliations",
  head: {
    title: "Affiliations",
    link: [
      {
        rel: "canonical",
        href: "https://www.travelfreakvacations.com/affiliations"
      }
    ],
    meta: [
      {
        name: "description",
        content:
          "We have a network across 7 continents. You name a destination of your choice and we will do everything for you."
      },
      // OpenGraph data (Most widely used)
      { property: "og:title", content: "Affiliations - Travelfreak Vacations" },
      { property: "og:site_name", content: "Travelfreak Vacations" },
      // The list of types is available here: http://ogp.me/#types
      { property: "og:type", content: "website" },
      // Should be the same as your canonical link, see below.
      {
        property: "og:url",
        content: "https://www.travelfreakvacations.com/affiliations"
      },
      {
        property: "og:image",
        content: "https://www.travelfreakvacations.com/images/tfv-logo.png"
      },
      // Often the same as your meta description, but not always.
      {
        property: "og:description",
        content:
          "We have a network across 7 continents. You name a destination of your choice and we will do everything for you."
      }
    ]
  },
  components: {
    CoverImage
  },
  created() {
    const plugin = document.createElement("script");
    plugin.setAttribute("src", "https://widget.getyourguide.com/v2/widget.js");
    plugin.async = true;
    plugin.defer = true;
    document.head.appendChild(plugin);
  }
};
</script>



